.keyframes(~'border-expand, from {box-shadow: 0 0 0 3px transparent;} to {box-shadow: 0 0 0 3px @{accentColor}; }');
.keyframes(~'border-shrink, from {box-shadow: 0 0 0 3px @{accentColor};} to {box-shadow: 0 0 0 3px transparent; border: 0 solid @{accentColor}; }');
.keyframes(~'btn-lift, from {transform: scale(1); opacity: 1; height: 100%; top: auto; bottom: 0;} to {transform: scale(1); opacity: 1; height: 0; top: auto; bottom: 0;}');
.keyframes(~'btn-fall, from {transform: scale(1); opacity: 1; height: 0; top: 0;} to { transform: scale(1); opacity: 1; height: 100%; top: 0;}');
.keyframes(~'si-button-pulse, from {box-shadow: 0 0 8px 6px @{accentColor} , 0 0 12px 14px @{accentColor} ;} to {box-shadow: 0 0 18px 6px transparentize(@{accentColor} , 1), 0 0 4px 71px transparentize(@{accentColor} , 1) ;}');


//========================================================== animation shake
.keyframes(~'shake, 0%, 100% {transform: translate3d(0, 0, 0);}10%, 30%, 50%, 70%, 90% {transform: translate3d(-10px, 0, 0);}20%, 40%, 60%, 80% {transform: translate3d(10px, 0, 0);}');

//========================================================== animation buzz-out
.keyframes(~'buzz-out, 10% { transform: translateX(3px) rotate(2deg)} 20% {transform: translateX(-3px) rotate(-2deg)}30% {transform: translateX(3px) rotate(2deg)}40% {transform: translateX(-3px) rotate(-2deg)}50% {transform: translateX(2px) rotate(1deg)}60% {transform: translateX(-2px) rotate(-1deg)}70% {transform: translateX(2px) rotate(1deg)}80% {transform: translateX(-2px) rotate(-1deg)}90% {transform: translateX(1px) rotate(0deg)}100% {transform: translateX(-1px) rotate(0deg)}');


@hovAnim {
  .buzz-out {
    .anim(transform, buzz-out 1 linear 1.0s, translate3d(0, 0, 0));
  }
}

