@import "_variables.less";
@import "@{nodeSrc}lesshat/lesshat";
@import "@{bowerSrc}resetless/reset";
@import "_media.less";
@import "_mixin";
@import "_type.less";
@import "_utils";
@import "_theme";

html {
  background: #fff;
  color: @fontColor;
  .d_font_m;
}
.section-indent {
    margin-top: @indentXs;
    margin-bottom: @indentXs*2;
  }
.container {
  .desktop-xs(@desktopXs); // width ****px
  .gutter(130px);
  display: block;
  margin: 0 auto;
  margin: 0 auto;
  position: relative;
  z-index: 10;
}
.row {
  .rowMargin(-@indentMd);
}
.col {
  .gutter(@indentMd);
}
.clearfix, .clearfix {
  &:before,
  &:after {
    clear: both;
    content: '';
    display: table;
  }
}

.small-caps {
  font-size: 75%;
  letter-spacing: .15em;
  margin-right: -.15em;
  text-transform: uppercase;
}


//==================== HEADERS
h1, h2, h3, h4 {
  position: relative;
}
h1 {
  .b_font_h1;
  &.heading {
    padding-top: @indentMd*4;
    padding-bottom: @indentMd*5;
  }
  .small {
    .b_font_s;
    margin-bottom: 6px;
  }
}
h2 {
  .b_font_h2;
  padding-top: @indentMd*2;
  padding-bottom: @indentMd*2;
  &.heading {
    padding-top: @indentMd*3;
    padding-bottom: @indentMd*6;
  }
  .big-text {
    .b_font_xxl;
  }
  .h2-text-small {
    display: block;
    .b_font_xl;
    position: relative;
    top: -2px;
  }
}
h3 {
  .b_font_h3;
  margin-bottom: @indentMd;
}
h4 {
  .b_font_h4;
  margin-top: @indentMd;
  margin-bottom: @indentMd;
}
.date {
  font-style: italic;
  .d_font_xs;
  &_fill {
    background-color: @accentColor;
    color: @fontColor;
    padding: 2px 8px 6px;
  }
}
.heading {
  &_fill {
    display: inline-block;
    background-color: @accentColor;
    color: @fontColor;
    margin-bottom: 4px;
    padding-left: 6px;
    padding-right: 18px;
    padding-top: 9px;
    vertical-align: top;
    // &_m {
    //   .b_font_h2;
    // }
    // &_line {
    //   position: relative;
    //   &:before {
    //     content: "";
    //     position: absolute;
    //     height: 4px;
    //     width: 28px;
    //     background: @accentColor;
    //     right: 100%;
    //     margin-right: 6px;
    //     top: 50%;
    //     margin-top: -2px;
    //   }
    // }
  }
  &_square {
    display: inline-block;
    width: 60%;
    height: 102px;
    background: #fff;
    border: 8px solid @accentColor;
    position: absolute;
    top: -25px;
    left: 63px;
    z-index: -1;
    &_xs {
      width: 100%;
    }
    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 100%;
      width: 0;
      height: 0;
      border-style: solid;
    }

    &::before {
      left: -8px;
      border-width: 48px 48px 0 0;
      border-color: @accentColor transparent transparent transparent;
      z-index: 1;
    }

    &::after {
      left: 0;
      border-width: 30px 30px 0 0;
      border-color: #fff transparent transparent transparent;
      z-index: 2;
    }

    &.big {
      width: 264px;
      height: 192px;
      top: -39px;
    }
  }
  &_decor {
    display: inline-block;
    position: relative;
    z-index: 1;
    &::before,
    &::after {
      .skewY(-5deg);
      content: "";
      position: absolute;
      width: 2000px;
      height: 1px;
      background: #e1e1e1;
      z-index: -1;
    }

    &::before {
      top: -77px;
      left: calc(100% + 10px);
    }

    &::after {
      top: 138px;
      right: calc(100% + 10px);
    }
  }
  &_icon {
    &.icon {
      font-size: 3em;
    }
    .m_posY(absolute);
    left: 100%;
    margin-left: 50px;
  }
}


//==================== PARAGRAPH
p {
  padding: 0;
  margin: 0 0 18px;
}

img {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

dl {
  margin: 0;
  padding: 0;
}

dt, dd {
  display: inline-block;
  margin: 0;
  padding: 0;
}



.dotted {
  &:extend(a);
  position: relative;
  overflow: hidden;
  padding-bottom: 10px;
  vertical-align: top;

  &::after {
    content: ".....................................................................................................................";
    display: inline-block;
    position: absolute;
    bottom: 0;
    left: 0;
    font-weight: 500;
  }
}

.dashed {
  &:extend(a);
  border-bottom: 1px dashed @linkColor;
}

.line {
  // &:extend(a);
  border-width: 1px;
  border-bottom-style: solid;
  border-color: inherit;
}
//====================navigation list
ul.nav {
  li {
    display: inline-block;
    list-style: none;
  }

  a, span {
    text-align: center;
    cursor: pointer !important;
  }
}
ul.not-marked {
  padding: 0;
  margin: 0;

  li {
    list-style: none;
  }
}
ul.marked {
  @extend p;
  li {
    list-style: none;
    padding-left: 20px;
    margin-bottom: 18px;
    position: relative;

    &::before {
      content: "";
      display: inline-block;
      width: 4px;
      height: 4px;
      background-color: @accentColor;
      border-radius: 50%;
      position: absolute;
      top: 10px;
      left: 0;
    }
  }
}



/// FROM FLEXGRID
.square {
  .paddingXY(@indentSm);
  background-color: Khaki;
  height: 20px;
  width: 70%;
}
.wrap {
  .square {
    height: auto;
    font-family: 'Zilla Slab', serif;
    font-size: 1vw;
    line-height: 1vw;
  }
  
  margin-top: 20px;
  margin-bottom: 20px;
}
.margin {
  .rowMargin(@indentMd);
}
.bg_triangle,
.bg_romb,
.bg_square {
  position: absolute;
  z-index: -1;
  opacity: .4;
}
.bg_triangle {
  // background-color: inherit;
  left: -13px;
  top: -22px;
  width: 120%;
  height: 110%;
  clip-path: polygon(50% 5%, 0% 100%, 100% 100%);
  &:after {
    content: '';
    position: absolute;
    width: calc(~'100% - 14px');
    height: calc(~'100% - 14px');
    left: 7px;
    top: 10px;
    clip-path: polygon(50% 5%, 0% 100%, 100% 100%);
    background-color: #fff;
  }
}
.bg_square {
  top: -9px;
  left: 7px;
}
.bg_square,
.bg_romb {
  border-width: 4px;
  border-color: inherit;
  border-style: solid;

  width: calc(~"100% - 15px");
  height: 100%;

}
.bg_romb {
  left: @indentSm*2;
  top: @indentSm;
  .rotate(45deg);
  width: 80%;
  height: 80%;
}